<template>
  <div
    class="Classifydetail"
    v-loading.fullscreen.lock="loading"
    element-loading-text="正在翻开绘卷"
    element-loading-spinner="el-icon-loading"
    element-loading-background="rgba(255, 255, 255, 0.8)"
  >
    <div class="">
      <Topnav class="fixedTop">
        <template #bookname>{{major}}</template>
      </Topnav>
      <div class="classtogle">
        <li @click="changetype('hot')" :class="{typeactive:type=='hot'}">
          热门
          <i class="el-icon-caret-bottom"></i>
        </li>
        <li @click="changetype('new')" :class="{typeactive:type=='new'}">
          新书
          <i class="el-icon-caret-bottom"></i>
        </li>
        <li @click="changetype('repulation')" :class="{typeactive:type=='repulation'}">
          好评
          <i class="el-icon-caret-bottom"></i>
        </li>
        <li @click="changetype('over')" :class="{typeactive:type=='over'}">
          完结
          <i class="el-icon-caret-bottom"></i>
        </li>
      </div>
      <Booklist :list="this.booklist"></Booklist>
      <span v-if="nomore">抱歉，没有更多书籍了</span>
      <i class="loading el-icon-loading" v-if="!nomore"></i>
    </div>
  </div>
</template>
<script>
import Booklist from "../components/Booklist.vue";
import Topnav from "../components/Topnav.vue";

export default {
  name: "Classifydetail",
  components: {
    Booklist,
    Topnav
  },
  inject: ["reload"],
  data() {
    return {
      gender: null,
      type: "hot",
      major: null,
      minor: null,
      start: 0,
      limit: 20,
      parmes: null,
      booklist: null,
      loading: true,
      nomore: false
    };
  },
  methods: {
    changetype(t) {
      this.type = t;
      this.$router.replace({
        name: "Classifydetail",
        query: {
          type: t,
          gender: this.parmes.gender,
          major: this.parmes.major,
          minor: this.parmes.minor
        }
      });
    }
  },
  created() {
    let parme = this.$route.query;
    this.parmes = parme;
    this.gender = this.parmes.gender;
    this.major = this.parmes.major;
    this.minor = this.parmes.minor;
    if (this.parmes.type) {
      this.type = this.parmes.type;
    }
    let url = `https://api.kele8.cn/agent?url=http://novel.juhe.im/category-info?gender=${
      this.gender
    }&type=${this.type}&major=${this.major}&minor=&start=${this.start}&limit=${
      this.limit
    }`;
    axios
      .get(url)
      .then(res => {
        this.loading = false;
        this.booklist = res.data.books;
        if (!res.data.books) {
          this.nomore = true;
        }
      })
      .catch(err => {
        console.error(err);
      });
  },
  mounted() {
    var canLoad = true;
    let self = this;
    let toload = document.querySelector(".loading");
    document.onscroll = function() {
      if (
        toload.offsetTop <=
        window.innerHeight + document.documentElement.scrollTop
      ) {
        if (canLoad == true) {
          canLoad = false;
          self.start += 21;
          var url = `https://api.kele8.cn/agent?url=http://novel.juhe.im/category-info?gender=${
            self.gender
          }&type=${self.type}&major=${self.major}&start=${self.start}&limit=${
            self.limit
          }`;
          axios
            .get(url)
            .then(res => {
              self.booklist = self.booklist.concat(res.data.books);
            })
            .catch(err => {
              console.error(err);
            });
        }
      }
    };
  },
  beforeRouteUpdate(to, from, next) {
    this.reload();
    next();
  }
};
</script>
<style scoped lang="less">
.fixedTop {
  width: 100%;
  background: #fff;
  position: fixed;
  top: 0;
}
.classtogle {
  width: 100%;
  top: 3rem;

  position: relative;
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
  margin-bottom: 0.25rem;
  li {
    background: #f9f9fc;
    line-height: 2rem;
    flex: 1;
  }
}
.typeactive{
  color: #ff3c3c;
}
.Booklist{
  margin-top: 3rem;
}
</style>
